//---------------------------- 변수 저장 ------------------------------
$redColor : #ff0000;

.red{
    color: $redColor;
}
//------------------------@extend : scss의 상속  -------------------------
.my-alert {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
    p{
        margin-bottom: 0;
    }
}
.my-alert-yellow {
    @extend .my-alert;
    background: #ffe591
}

//---------------------------- @mixin : 서랍장 --------------------------------
@mixin function1(){
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}
.my-alert-green {
    @include function1();
    background: green;
}